import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';

@Component({
  selector: 'lib-payment-details',
  templateUrl: './payment-details.component.html',
  styleUrls: ['./payment-details.component.scss']
})
export class PaymentDetailsComponent implements OnInit {

  paymentMethodType: any = 'Bank transfer';
  paymentEnable: any;
  settings: any;
  devicePayment: boolean=false;
  testResult: any="testResult";
  valid(paymentEnable: any) {
    this.paymentEnable = paymentEnable;
    this.payEmitter.emit(paymentEnable);
  }
  @Input() genericPaymentDetails:any;

  userData: any;
  @Input() tip: any;
  paymentMethodAllowed: any = 3;
  paymentMethod: any = 1;
  paymentCompleted: boolean = true;
  canDoWalletPay: boolean = false;
  walletPayLogoName: string = '';
  walletPayDesc: string = '';
  walletDetails: any;

  @Output() emitter: EventEmitter<string> = new EventEmitter<string>();
  @Output() payEmitter: EventEmitter<any> = new EventEmitter<any>();



  constructor() { }

  ngOnInit(): void {
  }

  paymentSelected(value: any) {
    var temp = this.paymentMethod != value
    if (value > 0) {
        this.paymentMethod = value;
        this.emitter.emit(value);
        var check = document.getElementById(value) as HTMLInputElement
        if (check) {
            check.checked = true;
        }
        if (temp) {
            this.payEmitter.emit(true);
        }
    }
}

}
